.widget-card {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.widget-toolbar {
  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
  color: white;
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 16px;
  font-size: 16px;
  font-weight: 500;

  mat-icon {
    font-size: 20px;
    width: 20px;
    height: 20px;
  }
}

.widget-content {
  flex: 1;
  padding: 16px;
  overflow-y: auto;
}

.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px 20px;
  color: #9e9e9e;

  mat-icon {
    font-size: 48px;
    width: 48px;
    height: 48px;
    margin-bottom: 16px;
  }

  p {
    margin: 0;
    font-size: 14px;
  }
}

.progress-summary {
  margin-bottom: 24px;

  .total-count {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px;
    background: linear-gradient(135deg, rgba(240, 147, 251, 0.1) 0%, rgba(245, 87, 108, 0.1) 100%);
    border-radius: 8px;

    .label {
      font-size: 14px;
      color: var(--text-secondary);
    }

    .value {
      font-size: 24px;
      font-weight: 600;
      color: var(--text-primary);
    }
  }
}

.progress-bars {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.progress-item {
  .progress-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;

    .status-label {
      font-size: 14px;
      font-weight: 500;
      color: var(--text-primary);
    }

    .status-value {
      font-size: 13px;
      color: var(--text-secondary);
    }
  }

  .progress-bar-container {
    width: 100%;
    height: 8px;
    background: rgba(0, 0, 0, 0.08);
    border-radius: 4px;
    overflow: hidden;

    .progress-bar {
      height: 100%;
      transition: width 0.3s ease;
      border-radius: 4px;
    }
  }

  &.receive .progress-bar {
    background: linear-gradient(90deg, #90CAF9 0%, #64B5F6 100%);
  }

  &.progress .progress-bar {
    background: linear-gradient(90deg, #FFF59D 0%, #FDD835 100%);
  }

  &.complete .progress-bar {
    background: linear-gradient(90deg, #A5D6A7 0%, #66BB6A 100%);
  }
}

:host-context(.dark-theme) {
  .progress-bar-container {
    background: rgba(255, 255, 255, 0.1);
  }
}
